import { createRouter, createWebHashHistory } from "vue-router";

const routes = [
  {
    path: '/',
    name: 'Index',
    component: () => import('@/views/index'),
    meta: {
      title: '',
      // keepAlive: true
    }
  },
  {
    path: '/swap',
    name: 'Swap',
    component: () => import('@/views/swap'),
    meta: {
      title: '兑换',
      // keepAlive: true
    }
  },
  {
    path: '/merge',
    name: 'Merge',
    component: () => import('@/views/merge'),
    meta: {
      title: '合成',
      // keepAlive: true
    }
  },
  {
    path: '/user',
    name: 'User',
    component: () => import('@/views/user'),
    meta: {
      title: '我的资产',
      // keepAlive: true
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router;
