
const intro = extend('intro', localStorage)
const locale = extend('locale', localStorage)
const initemStorage = extend('initemStorage', sessionStorage)
const netId = extend('netId', sessionStorage)
function setData (key, value, storage = localStorage) {
  try {
    storage.setItem(key, value)
  }
  catch (e) {
    console.log(e)
  }
}
function extend (key, storage = localStorage) {
  if (key === 'locale') {
    key = 'i18nextLng'
  }
  return {
    get () {
      return storage.getItem(key)
    },
    set (value) {
      setData(key, value, storage)
    },
    remove () {
      storage.removeItem(key)
    }
  }
}
export {
  intro,
  locale,
  initemStorage,
  netId
}
