import Web3 from "web3";
import store from "../store/index"
import { Toast } from 'vant';
import { useI18n } from "vue-i18n"
const web3Data = {
  web3: {},
  t: null,
  factoryContract: {},
  web3Provider: {},
  getBlance: (account) => {
    web3Data.web3.eth.getBalance(account).then((amount) => {
      store.commit(
        "updateCurrentBalance",
        web3Data.web3.utils.fromWei(amount, "ether")
      );
    });
  },
  getNetId: async () => {
    let id = await web3Data.web3.eth.net.getId()
    store.commit(
      "updateNetId",
      id
    );
  },
  initWeb3: () => {
    if (window.ethereum) {
      web3Data.web3Provider = window.ethereum;
      // window.ethereum.on("accountsChanged", async (accounts) => {
      //   store.commit("updateAccount", accounts);
      //   // window.location.reload()
      // })
      // window.ethereum.on('chainChanged', async () =>{
      //   await web3Data.getNetId()
      //   window.location.reload()
      // });
      web3Data.web3 = new Web3(web3Data.web3Provider);
      // try {
      //   await window.ethereum.enable();
      // } catch (error) {
      //   Promise.reject(error)
      // }
      window.ethereum.enable().then((data) => {
        console.log(data)
        store.commit("updateAccount", data);
      })
    } else {
      if (window.web3) {
        web3Data.web3Provider = window.web3.currentProvider;
        web3Data.web3 = new Web3(web3Data.web3Provider);
      } else {
        if (!web3Data.web3.t) {
          web3Data.web3.t = useI18n().t
        }
        Toast(web3Data.web3.t('web3.暫未檢測到錢包挿件，請安裝MetaMask錢包，或在imtoken裏打開'))
      }
    }
    store.commit("updateProvider", web3Data.web3);
    web3Data.web3.eth.getAccounts().then(async (accounts) => {
      store.commit("updateAccount", accounts);
      // await web3Data.getNetId()
      return web3Data.getBlance(accounts[0]);
    });
  }
}

export default web3Data;
